body {
  height: 100%;
  background: url(../src/p.photo.jpg);
}
header {
  padding: 10px;
  text-align: center;
  border: double;
  border-radius: 50px;
  background-color: rgb(10, 85, 85);
  color: aquamarine;
}
main {
  border-width: 8px;
  border-style: solid;
  border-color: white;
  box-shadow: 8px 8px 8px green;
  margin: 0 auto;
  display: block;
  text-align: center;
  padding: 80px;
}
input {
  padding: 10px;
  margin: 7px;
}
label {
  padding: 10px;
}
button {
  margin: 15px;
  margin-left: 50px;
  width: 100px;
  height: 40px;
  font-size: large;
  font-family: 'Times New Roman', Times, serif;
  cursor: pointer;
}
button:hover {
  background-color: rgb(10, 85, 85);
  color: aquamarine;
}
select {
  padding: 8px;
}
footer {
  text-align: center;
  padding: 10px;
  background-color: rgb(10, 85, 85);
  color: aquamarine;
  border: double;
  border-radius: 50px;
}
@media (min-width: 700px) {
  body {
    background: url(../src/tamimi.jpg);
    text-align: center;
    color: rgb(11, 182, 182);
    margin: 0 auto;
    height: 100%;
  }
  main {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  div {
    border-width: 8px;
    border-style: solid;
    border-color: white;
    box-shadow: 8px 8px 8px green;
    margin: 0 auto;
    width: 80%;
    display: block;
    text-align: center;
    padding: 50px;
  }
  p {
    color: antiquewhite;
    padding-left: 8px;
    display: block;
    font-size: large;
  }
}